export default {
	props:{
		themeColor:{
			type: String,
			default(){
				return "#6BA1FF"
			}
		},
		isCard:{
			type: Boolean,
			default(){
				return false
			}
		},
		formTemplate:{
			type: Array,
			default(){
				return []
			}
		},
		formValue:{
			type: Object,
			default(){
				return {}
			}
		},
		type:{
			type: String,
			default(){
				return "fill"
			}
		},
		time:{
			type: Object,
			default(){
				return {}
			}
		}
	},
	data() {
		const currentDate = this.getDate({
		            format: true
		})
		return {
			showItemIds: [],
			itemValue: {},
			date: currentDate
		}
	},
	watch:{
		formTemplate:function(){
			this.init();
		}
	},
	created() {
		this.init();
	},
	methods: {
		getDate(type) {
			const date = new Date();
			let year = date.getFullYear();
			let month = date.getMonth() + 1;
			let day = date.getDate();

			if (type === 'start') {
				year = year - 60;
			} else if (type === 'end') {
				year = year + 2;
			}
			month = month > 9 ? month : '0' + month;;
			day = day > 9 ? day : '0' + day;
			return `${year}-${month}-${day}`;
		},
		/**
		 * 处理ios键盘收回 界面不返回问题
		 * @param {Object} e
		 */
		onInputBlur(e) {
			if (isIOS) {
				var currentPosition, timer;
				var speed = 1;
				timer = setInterval(function() {
					currentPosition = document.documentElement.scrollTop || document.body.scrollTop;
					currentPosition -= speed;
					window.scrollTo(0, currentPosition); //页面向上滚动
					currentPosition += speed;
					window.scrollTo(0, currentPosition); //页面向下滚动
					clearInterval(timer);
				}, 100);
			}
		},
		
		/**
		 * 初始化数据
		 */
		init(){
			this.formTemplate.forEach(el => {
				this.showItemIds.push(el.id);
				el.object.forEach(el => {
					if(el.textName){
						this.$set(this.itemValue, el.textName, "");
					}
				});
			});
			// 如果传了初始值进行赋值
			if(this.formValue != {}){
				Object.assign(this.itemValue, this.formValue);
			}
		},
		/**
		 * 卡片显示影藏控制
		 * @param {Object} item
		 */
		showItemClick(item){
			if(this.showItemIds.indexOf(item.id) > -1){
				this.showItemIds.splice(this.showItemIds.indexOf(item.id), 1);
			}else{
				this.showItemIds.push(item.id);
			}
		},
		/**
		 * 日期选择器修改
		 * @param {Object} e
		 * @param {Object} textName
		 */
		datePickerChange(e, textName){
			this.itemValue[textName] = e.target.value;
		},
		/**
		 * 时间选择器修改
		 * @param {Object} e
		 * @param {Object} textName
		 */
		timePickerChange(e, textName){
			this.itemValue[textName] = e.target.value;
		},
		/**
		 * 单级选择器修改
		 * @param {Object} e
		 * @param {Object} textName
		 * @param {Object} values
		 */
		selectPickerChange(e, textName, values){
			this.itemValue[textName] = values[e.target.value].valueCode;
		},
		/**
		 * checkbox修改
		 * @param {Object} e
		 * @param {Object} textName
		 */
		checkboxChange(e, textName){
			this.itemValue[textName] = e.target.value.toString();
		},
		/**
		 * 单级select显示
		 * @param {Object} objects
		 * @param {Object} value
		 */
		selectPickerItemShow(objects, value){
			let name = '';
			objects.forEach(el => {
				if(el.valueCode == value){
					name = el.valueName;
				}
			});
			return name;
		},
		/**
		 * 输入校验
		 */
		inputValidation(){
			let checkFlag = true;
			let message = "校验成功";
			let value = {};
			
			this.formTemplate.forEach(el => {
				if(! checkFlag){
					return false;
				}
				el.object.forEach(el => {
					if(! checkFlag){
						return false;
					}
					if(el.isMustfill && this.itemValue[el.textName] == ""){ //必填
						message = "请输入"+el.subject;
						checkFlag = false;
						return false;
					}
					if(el.checkRegular){ //正则
						let reg = new RegExp(el.checkRegular);
						if(! reg.test(this.itemValue[el.textName])){
							message = el.subject+"输入不合法";
							checkFlag = false;
							return false;
						}
					}
				});
			});
			
			if(checkFlag){ value = this.itemValue; }
			return {checkFlag: checkFlag, message: message, value: value};
		},
		/**
		 * 暴露的提交方法
		 */
		submit(){
			return this.inputValidation();
		}
	}
}
